import Home from '@/view/web/home/index';
import Arrange from '@/view/web/arrange/index';
import Categories from '@/view/web/categories/index';
import About from '@/view/web/about/index';
import ArticleDetail from '@/view/web/articleDetail/index';
import AdminHome from '@/view/admin/home/index';
import ArticleEdit from '@/view/admin/articleEdit';
import UserList from '@/view/admin/userList';

export default {
    web: [
        {
            path: '/',
            title: '',
            exact: true,
            component: Home,
            isPrivate: false,
        },
        {
            path: '/arrange',
            title: '',
            exact: true,
            component: Arrange,
            isPrivate: false,
        },
        {
            path: '/categories',
            title: '',
            exact: true,
            component: Categories,
            isPrivate: false,
        },
        {
            path: '/about',
            title: '',
            exact: true,
            component: About,
            isPrivate: false,
        },
        {
            path: '/article',
            title: '',
            exact: true,
            component: Home,
            isPrivate: false,
        },
        {
            path: '/article/:id',
            title: '',
            exact: true,
            component: ArticleDetail,
            isPrivate: false,
        },
        {
            path: '/tag/:name',
            title: '',
            exact: true,
            component: Arrange,
            isPrivate: false,
        },
        {
            path: '/category/:name',
            title: '',
            exact: true,
            component: Arrange,
            isPrivate: false,
        },
    ],
    admin: [
        {
            path: '/admin',
            title: '',
            exact: true,
            component: AdminHome,
            isPrivate: false,
        },
        {
            path: '/admin/article/add',
            title: '',
            exact: true,
            component: ArticleEdit,
            isPrivate: false,
        },
        {
            path: '/admin/article/edit/:id',
            title: '',
            exact: true,
            component: ArticleEdit,
            isPrivate: false,
        },
        {
            path: '/admin/user/list',
            title: '',
            exact: true,
            component: UserList,
            isPrivate: false,
        },
    ]
}