import React, { Component } from 'react';
import { Input, Button } from 'antd';

class ArticleFilter extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: '',
        }
    }
    changeKeyword = (e) => {
        let keyword = e.target.value
        this.setState(() => ({
            keyword
        }))
    }
    submit = () => {
        let { getUsers } = this.props
        let { keyword } = this.state
        getUsers({ keyword })
    }
    render() {
        let { keyword } = this.state
        return (
            <div className='article-filter'>
                <div className='item'>
                    
                    <Input value={keyword} onChange={this.changeKeyword} placeholder="" className='keyword' />
                </div>
                <div className='item'>
                    <Button type="primary" onClick={this.submit}></Button>
                </div>
            </div>
        )
    }
}

export default ArticleFilter